<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/wechatAutoResponse/keywordReplyList" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>

    <div class="cms-form-layout">
      <el-form ref="form" :rules="rules" :model="formData" label-width="110px">
        <el-form-item label="规则名称" prop="wechatReplyContent.ruleName">
          <el-input v-model="formData.wechatReplyContent.ruleName" :autofocus="true" ref="first" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="匹配优先级" prop="wechatReplyContent.sortNum">
          <el-input v-model.number="formData.wechatReplyContent.sortNum" maxlength="2"></el-input>
          <div class="gray">在多组规则关键词都匹配的情况下，优先匹配优先级越大的</div>
        </el-form-item>
        <el-form-item label="是否禁用" prop="wechatReplyContent.isEnable">
          <cms-switch v-model="formData.wechatReplyContent.isEnable"></cms-switch>
          <div class="gray">设置为“是”，标识当前规则暂不生效</div>
        </el-form-item>
        <h3 class="cms-segmentation">关键词列表</h3>
        <el-table ref='dataTable' :data="keywordList" class="keyword-table">
          <el-table-column label="关键词">
            <div slot-scope="scope">
              <el-input v-model="scope.row.value" style="width:60%" placeholder="请输入匹配的关键词" maxlength="50"></el-input>
            </div>
          </el-table-column>
          <el-table-column prop="matchingRule" label="匹配规则">
            <div slot-scope="scope">
              <cms-switch v-model="scope.row.type" on-text='完全' off-text='模糊' size='large'></cms-switch>
            </div>
          </el-table-column>
          <el-table-column label="操作" width='175' fixed="right" align="center">
            <div slot-scope="scope">
              <cms-button icon='error' size='small' type='info' @click.stop="deleteData(scope.$index)">删除</cms-button>
            </div>
          </el-table-column>
        </el-table>
        <div @click="addKeyword()" class="addKeyword">＋ 添加关键词</div>
        <div style="width:100%;cursor:pointer;">
          <h3 class="cms-segmentation">触发后回复内容
            <!-- <span style="font-size:12px;margin-left:15px">只能设置一条</span> -->
          </h3>
          <media-warpper :restData="restData" :show="true" @change="getMediaData"  style="margin:0" v-if="status"></media-warpper>
        </div>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(false)" v-color>保存并新增</cms-button>
        <cms-button @click="save(true)" v-color>保存</cms-button>
        <cms-button type='rest' @click="rest">重置</cms-button>
        <!-- <cms-button type='rest' @click="test">mock测试</cms-button> -->
      </div>
    </div>
  </div>
</template>
<script>
import form from '@/mixins/form.js'
import mediaWarpper from './../materialLibrary/components/mediaWarpper'
import { keywordSave } from '@/api/wechat/autoResponse.js'
import rules from '@/rules'
var defaultData = {
  keyEq: [],
  keyLike: [],
  wechatReplyContent: {
    'thumbMediaId': '',
    'hqMusicUrll': '',
    'musicUrl': '',
    'description': '',
    'title': '',
    'content': '',
    'isEnable': false,
    'msgType': '',
    'ruleName': '',
    'sortNum': null,
    'mediaid': ''
  }
}
export default {
  name: 'keywordSave',
  mixins: [form],
  components: {
    mediaWarpper
  },
  data() {
    return {
      formData: Object.assign({}, defaultData),
      keywordList: [],
      rules: {// 新增规则
        'wechatReplyContent.ruleName': [rules.required('请填写规则名称')],
        'wechatReplyContent.sortNum': [rules.required('请填写匹配优先级'), rules.number()]
      },
      restData: false
    }
  },
  methods: {
    save(state) {
      const reply = this.formData.wechatReplyContent
      const keyEq = []
      const keyLike = []
      this.keywordList.forEach(v => {
        if (v.type) {
          keyEq.push(v.value)
        } else {
          keyLike.push(v.value)
        }
      })
      this.formData.keyEq = keyEq
      this.formData.keyLike = keyLike
      if (reply.mediaid === '' && (reply.musicUrl === '' && reply.hqMusicUrll === '') && (!reply.content || reply.content === '')) {
        this.$errorMessage('关键词必须添加一个回复内容')
      } else {
        if (reply.thumbMediaId === '' && reply.msgType === 'music') {
          this.$errorMessage('请选择资源空间内音乐文件')
        } else {
          this.saveForm(
            keywordSave,
            this.formData,
            state,
            '/wechatAutoResponse/keywordReplyList',
            'list'
          )
        }
      }
    },
    deleteData(index) {
      this.keywordList.splice(index, 1)
    },
    addKeyword() {
      this.keywordList.push({
        value: '',
        type: false
      })
    },
    getMediaData(items) {
      if (items[0]) {
        this.formData.wechatReplyContent.mediaid = items[0].id
        this.formData.wechatReplyContent.msgType = items[0].mediaType
      } else {
        this.formData.wechatReplyContent.hqMusicUrll = items.hqMusicUrll
        this.formData.wechatReplyContent.musicUrl = items.musicUrl
        this.formData.wechatReplyContent.thumbMediaId = items.thumbMediaId
        this.formData.wechatReplyContent.description = items.description
        this.formData.wechatReplyContent.title = items.title
        this.formData.wechatReplyContent.content = items.content
        this.formData.wechatReplyContent.msgType = items.msgType
      }
    },
    rest() {
      this.$refs.form.resetFields()
      this.formData = Object.assign({}, defaultData)
      this.formData.wechatReplyContent.hqMusicUrll = ''
      this.formData.wechatReplyContent.musicUrl = ''
      this.formData.wechatReplyContent.description = ''
      this.formData.wechatReplyContent.title = ''
      this.formData.wechatReplyContent.content = ''
      this.formData.wechatReplyContent.msgType = null
      this.formData.wechatReplyContent.mediaid = ''
      this.formData.wechatReplyContent.thumbMediaId = ''
      this.keywordList = []
      this.restData = true
    }
  },

  created() {}
}
</script>
<style scoped>
.keyword-table {
  width: 100%;

  border-left: 1px solid #f7f7f7;
  border-right: 1px solid #f7f7f7;
}
.type_ul {
  padding: 20px;
  list-style: none;
}
.type_ul li {
  width: 100px;
  height: 50px;
  text-align: center;
  float: left;
}
.type_ul li img {
  width: 30px;
  height: 30px;
}
.type_p {
  font-size: 18px;
  color: #000000;
}
.type_p span {
  font-size: 14px;
  color: #646464 !important;
}
.addKeyword {
  width: 100%;
  font-size: 14px;
  cursor: pointer;
  border: 1px solid #f7f7f7;
  height: 30px;
  text-align: center;
  line-height: 30px;
}
</style>



